body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.react-datepicker {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  background-color: #ffffff;
  border: none;
  border-radius: unset;
  font-weight: 500;
  line-height: normal;
